// -------- Ostrich Black -------- //

// Heavy
@font-face {
	font-family: 'ostrich';
	src: '../fonts/OstrichSans-Heavy.otf';
}

// -------- Futura -------- //

// Book
@font-face {
	font-family: 'futura';
